@import '../../style/variables.sass'
 
.char-list
    display: grid
    grid-template-columns: repeat(3, 200px)
    gap: 30px 25px
    &__item
        height: 318px
        background: $dark
        transition: .3s all ease-in
        cursor: pointer
        img
            width: 200px
            height: 200px
        &_active
            filter: drop-shadow(0px 5px 20px #9F0013)
            transform: translateY(-9px)
        &-enter
            opacity: 0
        &-enter-active 
            opacity: 0
            transition: opacity 200ms 
        &-exit 
            opacity: 1 
        &-exit-active 
            opacity: 0
            transition: opacity 200ms
              
    &__name
        color: #fff
        margin-left: 15px
        margin-top: 15px

.btn__wrapper
    display: flex
    justify-content: center
    width: 100%
    margin-top: 45px